import { gsap, SplitText } from 'vendors/gsap';

export default class ZigZagAnimation implements AnimationInterface {
  el?:HTMLElement;
  split?:SplitText;
  tl?:Timeline;

  constructor(els:{
    el?:HTMLElement,
  }) {
    this.el = els.el;

    this.init();

    return this;
  }

  init() {
    if (this.el) {

      this.split = new SplitText(this.el, {
        type: 'lines,words',
        linesClass: 'line',
        wordsClass: 'word',
        tag: 'span',
        reduceWhiteSpace: false,
      });

      this.tl = gsap.timeline(
        { 
          paused: true,
          autoRemoveChildren: false,
          repeatRefresh: true,
        }
      )
        .fromTo(this.split.words, 
          {
            rotate: (index) => {
              const rand = index % 2
              return rand ? 20 : -30; 
            },
            y: '6em'
          },
          { 
            rotate: 0, 
            y: 0, 
            delay: 0.5,
            duration: 0.9, 
            stagger: {
              amount: 0.4,
            },
            ease: 'power2.out'
          }
      );
    }
  }

  enter = () => {
    if (this.tl) {
      this.tl.timeScale(1).play(0)
    }
  }

  display = () => {
    if (this.tl) {
      this.tl.totalProgress(1, false);
    }
  }
  update = () => {
    if (this.el) {
      this.split = new SplitText(this.el, {
        type: 'lines,words',
        linesClass: 'line',
        wordsClass: 'word',
        tag: 'span',
        reduceWhiteSpace: false,
      });
    }
  }

  leave = () => {
    if (this.tl) {
      this.tl.timeScale(2).reverse()
    }
  }

  dispose() {
    this.split?.revert();
    this.tl?.kill();
    this.tl = undefined;
    this.split = undefined;
  }
}
